import { ConfigurationService } from "../common/configuration.service";
import { CollectionService, Item } from "../common/collection.service";
import { CollectionComponent } from '../common/collection.component';
import { CategoryAxisItemComponent } from './category-axis-item.component';

/**
 * @hidden
 */
export abstract class CategoryAxisComponentGenerated extends CollectionComponent {
  @ContentChildren(CategoryAxisItemComponent)
  protected children: QueryList<Item>;

  constructor(protected configurationService: ConfigurationService,
              protected collectionService: CollectionService) {
    super('categoryAxis', configurationService, collectionService);
  }
}




